import React from "react";
import {
    List, Datagrid, TextField, SearchInput, TextInput, Filter, Create, SimpleForm, Edit,
    Show, SimpleShowLayout, EditButton, TopToolbar, Button, ShowButton,
    NumberInput, DeleteButton
} from "react-admin";

const LieuxFilter = (props) => (
    <Filter {...props}>
        <SearchInput source="q" alwaysOn />
    </Filter>
);

export const LieuxList = (props) => (
    <List
        {...props}
        filters={<LieuxFilter />}
        filterDefaultValues={{ is_published: true }}
    >
        <Datagrid rowClick="show">
            <TextField source="id" />
            <TextField source="name" />
            <TextField source="ville" />
            <TextField source="capacite" />
            <TextField source="prix" />
            <ShowButton />
            <EditButton />
            <DeleteButton />
        </Datagrid>
    </List>
);

export const LieuxCreate = (props) => (
    <Create {...props}>
        <SimpleForm>
            <TextInput source="name" />
            <TextInput source="ville" />
            <NumberInput source="capacite" />
            <NumberInput source="prix" />
        </SimpleForm>
    </Create>
);

const LieuxEditActions = ({ basePath, data, resource }) => (
    <TopToolbar>
        <ShowButton basePath={basePath} record={data} />
    </TopToolbar>
);


export const LieuxEdit = (props) => (
    <Edit actions={<LieuxEditActions />} {...props}>
        <SimpleForm>
            <TextInput source="name" />
            <TextInput source="ville" />
            <NumberInput source="capacite" />
            <NumberInput source="prix" />
        </SimpleForm>
    </Edit>
);

const LieuxShowActions = ({ basePath, data, resource }) => (
    <TopToolbar>
        <EditButton basePath={basePath} record={data} />
        <Button color="primary">
            Custom Action
    </Button>
    </TopToolbar>
);

export const LieuxShow = (props) => (
    <Show {...props}>
        <SimpleShowLayout>
            <TextField source="id" />
            <TextField source="name" />
            <TextField source="ville" />
            <TextField source="capacite" />
            <TextField source="prix" />
        </SimpleShowLayout>
    </Show>
);