import * as React from "react";
import { fetchUtils, Admin, Resource } from "react-admin";
import AllInboxIcon from "@material-ui/icons/AllInbox";
import authProvider from './authProvider';
import { AvisList, AvisCreate, AvisShow, AvisEdit } from "./Components/Avis";
import { ProduitList, ProduitCreate, ProduitShow, ProduitEdit } from "./Components/Produit";
import { MessagesList, MessagesCreate, MessagesShow, MessagesEdit } from "./Components/Messages";
import { UsersList, UsersShow } from "./Components/Users";
import frenchMessages from "ra-language-french";
import polyglotI18nProvider from "ra-i18n-polyglot";
import PersonIcon from "@material-ui/icons/Person";
import MessageIcon from "@material-ui/icons/Message";
import FeedbackIcon from "@material-ui/icons/Feedback";
import LocalMallIcon from "@material-ui/icons/LocalMall";
import EventIcon from '@material-ui/icons/Event';
import RestaurantMenuIcon from '@material-ui/icons/RestaurantMenu';
import { createMuiTheme } from '@material-ui/core/styles';
import PlaceIcon from '@material-ui/icons/Place';
import pink from '@material-ui/core/colors/pink';
import blue from '@material-ui/core/colors/blue';
import { LieuxList, LieuxCreate, LieuxShow, LieuxEdit } from "./Components/Lieux";
import { EventList, EventCreate, EventShow, EventEdit } from "./Components/Evenement";
import { MenuList, MenuCreate, MenuShow, MenuEdit } from "./Components/Menus";
import { CommandeList, CommandeCreate, CommandeShow, CommandeEdit } from "./Components/Commande";
import { dataProvider } from "./dataProvider";

const i18nProvider = polyglotI18nProvider(() => frenchMessages, "fr");


const theme = createMuiTheme({
  palette: {
    primary: blue,
    secondary: pink,
  },
});


const App = () => (
  <Admin
    title="Clic'n Fest Admin"
    locale="fr"
    i18nProvider={i18nProvider}
    dataProvider={dataProvider}
    authProvider={authProvider}
    theme={theme}
  >
    {(permissions) => [
      <Resource
        name="Produit"
        icon={LocalMallIcon}
        list={ProduitList}
        create={ProduitCreate}
        show={ProduitShow}
        edit={ProduitEdit}
      />,
      <Resource
        options={{ label: "Lieux", title: "Lieux" }}
        name="Lieux"
        icon={PlaceIcon}
        list={LieuxList}
        create={LieuxCreate}
        show={LieuxShow}
        edit={LieuxEdit}
      />,
      <Resource
        name="Messages"
        icon={MessageIcon}
        list={MessagesList}
        create={MessagesCreate}
        show={MessagesShow}
        edit={MessagesEdit}
      />,
      <Resource
        name="Avis"
        icon={FeedbackIcon}
        list={AvisList}
        create={AvisCreate}
        show={AvisShow}
        edit={AvisEdit}
      />,
      <Resource
        name="Menus"
        icon={RestaurantMenuIcon}
        list={MenuList}
        create={MenuCreate}
        show={MenuShow}
        edit={MenuEdit}
      />,
      permissions === "Admin" ? (
        <Resource
          name="Users"
          icon={PersonIcon}
          list={UsersList}
          show={UsersShow}
        />
      ) : null,
      permissions === "Admin" ? (
        <Resource
          name="Evenements/EvenementsListe"
          options={{ label: "Evenements" }}
          icon={EventIcon}
          list={EventList}
          create={EventCreate}
          show={EventShow}
          edit={EventEdit}
        />
      ) : null,
      permissions === "Admin" ? (
          <Resource
            name="Commandes/AllCommande"
            options={{ label: "Commandes" }}
            icon={AllInboxIcon}
            list={CommandeList}
            show={CommandeShow}
            edit={CommandeEdit}
          />
      ) : null,
    ]}
  </Admin>
);
export default App;
