import React from "react";
import {
  List,
  Datagrid,
  TextField,
  SearchInput,
  TextInput,
  Filter,
  Create,
  SimpleForm,
  Edit,
  Show,
  SimpleShowLayout,
  EditButton,
  TopToolbar,
  Button,
  ShowButton,
  DateInput,
  DeleteButton,
} from "react-admin";

const CommandeFilter = (props) => (
  <Filter {...props}>
    <SearchInput source="q" alwaysOn />
  </Filter>
);

export const CommandeList = (props) => (
  <List
    {...props}
    filters={<CommandeFilter />}
    filterDefaultValues={{ is_published: true }}
  >
    <Datagrid rowClick="show">
      <TextField source="id" />
      <TextField source="name" />
      <TextField source="creationDate" />
      <TextField source="confirmation" />
      <TextField source="isPaid" />
      <TextField source="evennementId" />
      <TextField source="userId" />
      <ShowButton />
      <EditButton />
      <DeleteButton />
    </Datagrid>
  </List>
);


const CommandeEditActions = ({ basePath, data, resource }) => (
  <TopToolbar>
    <ShowButton basePath={basePath} record={data} />
  </TopToolbar>
);

export const CommandeEdit = (props) => (
  <Edit actions={<CommandeEditActions />} {...props}>
    <SimpleForm>
      <TextField source="id" />
      <TextField source="name" />
      <TextField source="creationDate" />
      <TextField source="confirmation" />
      <TextField source="isPaid" />
      <TextField source="evennementId" />
      <TextField source="userId" />
    </SimpleForm>
  </Edit>
);


export const CommandeShow = (props) => (
  <Show {...props}>
    <SimpleShowLayout>
      <TextField source="id" />
      <TextField source="name" />
      <TextField source="creationDate" />
      <TextField source="confirmation" />
      <TextField source="isPaid" />
      <TextField source="evennementId" />
      <TextField source="userId" />
    </SimpleShowLayout>
  </Show>
);
