import React from "react";
import {
    List, Datagrid, TextField, SearchInput, TextInput, Filter, Create, SimpleForm, Edit,
    Show, SimpleShowLayout, EditButton, TopToolbar, Button, ShowButton,
    DeleteButton, NumberInput, SelectInput, ArrayInput, SimpleFormIterator,
    ReferenceManyField, TabbedShowLayout, Tab,
} from "react-admin";
import { dataProvider } from "../dataProvider";

const MenuFilter = (props) => (
    <Filter {...props}>
        <SearchInput source="q" alwaysOn />
    </Filter>
);

export const MenuList = (props) => (
    <List
        {...props}
        filters={<MenuFilter />}
        filterDefaultValues={{ is_published: true }}
    >
        <Datagrid rowClick="show">
            <TextField source="id" />
            <TextField source="name" />
            <TextField source="description" />
            <TextField source="prix" />
            <ShowButton />
            <EditButton />
            <DeleteButton />
        </Datagrid>
    </List>
);



export const MenuCreate = (props) => {
    const [produits, setProduits] = React.useState()
    dataProvider.getList('produit', {
        pagination: { page: 1, perPage: 5 },
        sort: { field: 'name', order: 'ASC' },
    })
    .then(response => {
        setProduits(response.data);
    })

    return(
        <Create {...props}>
            <SimpleForm>
                <TextInput source="name" />
                <TextInput source="description" />
                <ArrayInput source="createMenuItems" label="Ajouter un produit" parse>
                    <SimpleFormIterator>
                        <SelectInput source="produitId" label="produit" choices={produits} />
                        <TextInput source="quantite" label="quantitÃ©" />
                        <TextInput source="prix" label="prix produit" />
                    </SimpleFormIterator>
                </ArrayInput>
                <NumberInput source="prix" />
            </SimpleForm>
        </Create>
    )
}


const MenuEditActions = ({ basePath, data, resource }) => (
    <TopToolbar>
        <ShowButton basePath={basePath} record={data} />
    </TopToolbar>
);


export const MenuEdit = (props) => (
    <Edit actions={<MenuEditActions />} {...props}>
        <SimpleForm>
            <TextInput source="name" />
            <TextInput source="description" />
            <TextInput source="prix" />
        </SimpleForm>
    </Edit>
);

const MenuShowActions = ({ basePath, data, resource }) => (
    <TopToolbar>
        <EditButton basePath={basePath} record={data} />
        <Button color="primary">
            Custom Action
    </Button>
    </TopToolbar>
);

export const MenuShow = (props) => (
    <Show {...props}>
        <TabbedShowLayout>
            <Tab label="Menu">
                <TextField source="id" />
                <TextField source="name" />
                <TextField source="description" />
                <TextField source="prix" />
            </Tab>
            <Tab label="Menu item">
                    <TextField source="produitId" />
                    <TextField source="quantite" />
                    <TextField source="prix" />
            </Tab>
        </TabbedShowLayout>
    </Show>
);