import React from "react";
import {
    List, Datagrid, TextField, SearchInput, TextInput, Filter, Create, SimpleForm, Edit,
    Show, SimpleShowLayout, EditButton, TopToolbar, Button, ShowButton, DateInput,
    DeleteButton
} from "react-admin";

const EventFilter = (props) => (
    <Filter {...props}>
        <SearchInput source="q" alwaysOn />
    </Filter>
);

export const EventList = (props) => (
    <List
        {...props}
        filters={<EventFilter />}
        filterDefaultValues={{ is_published: true }}
    >
        <Datagrid rowClick="show">
            <TextField source="id" />
            <TextField source="type" />
            <TextField source="theme" />
            <TextField source="status" />
            <TextField source="startDate" />
            <TextField source="endDate" />
            <ShowButton />
            <EditButton />
            <DeleteButton />
        </Datagrid>
    </List>
);

export const EventCreate = (props) => (
    <Create {...props}>
        <SimpleForm>
            <TextInput source="type" />
            <TextInput source="theme" />
            <TextInput source="description" />
            <DateInput source="startDate" />
            <DateInput source="endDate" />
        </SimpleForm>
    </Create>
);

const EventEditActions = ({ basePath, data, resource }) => (
    <TopToolbar>
        <ShowButton basePath={basePath} record={data} />
    </TopToolbar>
);


export const EventEdit = (props) => (
    <Edit actions={<EventEditActions />} {...props}>
        <SimpleForm>
            <TextInput source="type" />
            <TextInput source="theme" />
            <TextInput source="status" />
            <TextInput source="startDate" />
            <TextInput source="endDate" />
        </SimpleForm>
    </Edit>
);

const EventShowActions = ({ basePath, data, resource }) => (
    <TopToolbar>
        <EditButton basePath={basePath} record={data} />
        <Button color="primary">
            Custom Action
    </Button>
    </TopToolbar>
);

export const EventShow = (props) => (
    <Show {...props}>
        <SimpleShowLayout>
            <TextField source="id" />
            <TextField source="type" />
            <TextField source="theme" />
            <TextField source="status" />
            <TextField source="description" />
            <TextField source="startDate" />
            <TextField source="endDate" />
            <TextField source="participant" />
        </SimpleShowLayout>
    </Show>
);